import React from 'react';
import Iframe from 'react-iframe'
import '../Home.scss';
import './Model.scss';

const projects = [
    {
        img: [
            { url: require('./img/lab.jpg') },
            { url: require('./img/speaker.jpg') },
            { url: require('./img/genshin_chest.jpg') },
            { url: require('./img/bike.jpg') },
            { url: require('./img/samurai_helmet.jpg') },
        ]
    },
]

function Model() {
    return (
        <div className="sectionModel">
            <div className="section_title">
                <div className="diamond"><div className="diamond_inner"></div></div>
                <h className="section_title_text"><span>3d modeling</span></h>
                <div className="diamond"><div className="diamond_inner"></div></div>
            </div>
            <div className="model_display">
                {
                    projects.map((item) =>
                        <div>
                            <div className="model_display_container">
                                <div className="model_display_content">
                                    {item.img?.map((i) =>
                                        <img src={i.url} className="model_display_content_img" />
                                    )}
                                </div>
                            </div>
                        </div>
                    )
                }
            </div>
            <br></br>
        </div>
    )
}

export default Model;

