import React, { useState } from 'react';
//import '../css/Home.scss';
import './Websites.scss'

const projects = [
    {
        name: "Kingpin",
        img: require("./img/kingpinSC1.png"),
        url: "https://moreminttea.github.io/kingpin/#/",
        description: "Kingpin is a collaboration website project dedicated to fantasy world-building, character design, and story writing."
    },
    {
        name: "JO1",
        img: require("./img/header.png"),
        url: "",
        description: "art site wip"
    },
]

function Websites() {
    const [current, setCurrent] = useState(0);
    const [first, setFirst] = useState(true);
    const [last, setLast] = useState(false);

    const _onClick = (next) => {
        next === 0 ? setFirst(true) : setFirst(false);
        next === projects.length - 1 ? setLast(true) : setLast(false);
        setCurrent(next);

        if (first && current === 0) setCurrent(1);
        if (last && current === 1) setCurrent(0);
    };

    return (
        <div className="sectionWebsites">
            <div className="section_title">
                <div className="diamond"><div className="diamond_inner"></div></div>
                <h className="section_title_text"><span>websites</span></h>
                <div className="diamond"><div className="diamond_inner"></div></div>
            </div>
            <div className="websites_display">
                <img
                    src={require("../../img/arrow.svg").default}
                    className="websites_display_arrows"
                    disabled={first}
                    onClick={() => _onClick(current - 1)} />
                <div className="prototypes_display_item">
                    <div className="websites_display_container">
                        <div className="websites_display_content">
                            {projects[current].url === ""
                                ? <div>
                                    <img src={projects[current].img} className="websites_display_content_img" alt="kingpin" />
                                    <div className="websites_display_content_description">{projects[current].description}</div>
                                </div>
                                : <a target="_blank" rel="noopener noreferrer" href={projects[current].url} style={{ textDecoration: 'none' }}>
                                    <img src={projects[current].img} className="websites_display_content_img" alt="website content" />
                                    <div className="websites_display_content_description">{projects[current].description}</div>
                                </a>
                            }
                        </div>
                    </div></div>
                <img
                    src={require("../../img/arrow.svg").default}
                    className="websites_display_arrows right_arrow"
                    disabled={last}
                    onClick={() => _onClick(current + 1)} />
            </div>

        </div>
    )
}

export default Websites;